// original.js
class Calculator {
  constructor(initialValue = 0) {
    this.value = initialValue;
  }

  add(num) {
    this.value += num;
    return this;
  }

  subtract(num) {
    this.value -= num;
    return this;
  }

  multiply(num) {
    this.value *= num;
    return this;
  }

  divide(num) {
    // Akan error jika num = 0
    if (num === 0) {
      throw new Error("Tidak bisa membagi dengan angka 0!");
    }
    this.value /= num;
    return this;
  }

  getResult() {
    return this.value;
  }
}

// Fungsi untuk membuat error
function createError() {
  try {
    const calculator = new Calculator(10);
    // Coba membagi dengan 0 untuk menghasilkan error
    calculator.divide(0);
  } catch (error) {
    console.error("Terjadi error:", error.message);
    throw error; // Re-throw error agar bisa ditangkap oleh LogRaven
  }
}

// Export fungsi untuk digunakan
if (typeof module !== 'undefined') {
  module.exports = {
    Calculator,
    createError
  };
} 